.messenger-box {
  box-sizing: border-box;
  position: fixed;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  top: 0px;
  left: 0px;
  background-color: lightsteelblue;
}

.messenger-header {
  box-sizing: border-box;
  width: 100%;
  padding: 10px;
  margin: 0;
  outline: 0;
  overflow: auto;
  background-color: lightyellow;
  text-align: left;
}

.messenger-header button {
  width: 5vh;
  height: 5vh;
  font-size: 1.5rem;
  background-color: grey;
  margin: 10px;
  vertical-align: middle;
  float: left;
}

.messenger-header div {
  height: 8vh;
}

.messenger-header h1 {
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  float: left;
  font-size: 1.5rem;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.messenger-header button p {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.messages {
  position: relative;
  box-sizing: border-box;
  padding: 20px;
  width: 100%;
  height: calc(100% - 8vh - 20px);
  overflow: auto;
  background-color: khaki;
}

.textfield {
  box-sizing: border-box;
  width: 100%;
  position: absolute;
  bottom: 20px;
}

.textfield span {
  display: block;
  width: 50px;
  height: 50px;
  margin-right: 1%;
  text-align: center;
  float: left;
  background-color: grey;
}

.textfield span p {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.textfield input {
  float: left;
  width: calc(97% - 210px);
  padding: 0 10px;
  margin-right: 1%;
  height: 50px;
  background-color: white;
}

.all-chats {
  height: calc(100% - 70px);
  margin-bottom: 10px;
  padding: 5px 0;
  background-color: aquamarine;
}

.all-chats p {
  margin: 10px 0;
  padding: 0 10px;
}

.all-chats > .left {
  text-align: left;
}

.all-chats > .right {
  text-align: right;
}
